<template>
    <div id="section1" style="margin-top: 250px; display: flex; align-items: center; justify-content: center;">
        <img class="landing-image" src="../assets/images/landingImage.png" alt="landingpage-image"/>
        <div class="scroll-message" id="mention">
            <h1 style="cursor: pointer" @click="fillPrgsBar()">Click or Touch Me!</h1>
            <progress id="progressbar" :value="prgsGage" max="100" ></progress>
        </div>
      </div>
</template>

<script>
export default {
    name: 'Landing',
    data() {
        return {
            prgsGage: 0,
        }
    },
    methods: {
        fillPrgsBar() {
            this.prgsGage += 10
        }
    }

}
</script>

<style>
    @font-face {
        font-family: 'solomonP';
        src: url('../font/SolomonP.ttf') format('truetype');
    }
    body {
        margin: 0;
    }
    .landing-image {
        height: 250px;
        width: 250px;
        margin: 0 auto;
        animation-name: stretch;
        animation-duration: 5s; 
        animation-timing-function: ease-out; 
        animation-delay: 0;
        animation-direction: alternate;
        animation-iteration-count: infinite;
        animation-fill-mode: none;
        animation-play-state: running;
    }

    @keyframes stretch {
        0% {
            transform: scale(.3);
        }
        100% {
            transform: scale(2.5);
        }
    }

    .scroll-message {
        font-family: solomonP;
        letter-spacing: 10px;
        color: rgba(255, 100, 150, 0.8);
        -webkit-animation: fadein 8s;
        position: absolute;
        text-align: center;
    }

    @keyframes fadein {
        from { opacity: 0; }
        to   { opacity: 1; }
    }
</style>