<template>
    <div>
         <div style="height:60px;"></div>
        <header class="header">
          <span class="bookmark-icon" @click="addBookmark">
            <i class="fa fa-bookmark"></i>
          </span>

          <div class="blog-title">
              <p class="blog-info">Development Study Log & Portfolio</p>
              <router-link to="/"><h1 class="blog-name">LIA LAND</h1></router-link>
          </div>

          <div class="toggle-button">
              <i @click="showNavItems" class="fa fa-bars"></i>
          </div>

          <nav>
              <ul class="nav-bar">
                  <li class="nav-item"><router-link to="/"><i class="fa fa-home"></i></router-link></li>
                  <li class="nav-item"><router-link to="/post">About Me</router-link></li>
                  <li class="nav-item"><router-link to="/post">Post</router-link></li>
                  <li class="nav-item"><router-link to="/portfolio">Portfolio</router-link></li>
              </ul>
          </nav>
        </header>
    </div>
</template>

<script>

    export default {
        name: 'Header',
        props: {

        },
        methods: {
            showNavItems() {
                const navItems = document.getElementsByClassName("nav-item");
                for (let i=0; i< navItems.length; i++) {
                    if (navItems[i].style.display === "none" || navItems[i].style.display === "") {
                        navItems[i].style.display = "block";
                    } else {
                        navItems[i].style.display = "";
                    }
                }
            },

            addBookmark() {
              alert("즐겨찾기에 추가하시려면 키보드의 Ctrl + D 를 눌러주세요. (Chrome 브라우저 기준)")

            },
        }
    }
</script>

<style>
  .nav-item {
    colo: black;
  }
  .header {
      background: ivory;
      opacity: 0.9;
      padding: 5px 5px;
      text-align: center;
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 1;
  }

  .bookmark-icon:hover {
    color: red;
    cursor: pointer;
  }

  .blog-info {
      color: gray;
      margin-bottom: 10px;
  }

  .blog-name {
    font-size: 25px;
    font-family: 'Racing Sans One', cursive;
  }

  @media screen and (min-width: 600px) {
      .nav-bar {
          list-style-type: none;
          display: flex;
          flex-direction: row;
          justify-content: center;
      }
      .nav-bar .nav-item {
          font-size: 16px;
          margin-right: 15px;
      }

      .toggle-button {
          display: none;
      }
  }

  @media screen and (max-width: 600px) {
      .nav-bar .nav-item {
          display: none;
          text-align: right;
      }

      .toggle-button {
          display: flex;
          justify-content: flex-end;
          cursor: pointer;
      }
  }

</style>
